import React from "react"

const Container = styled.div.attrs({ className: "" })``
const Text = styled.span.attrs({ className: "" })``
const Time = styled.span.attrs({ className: "" })``

const CountDownItem = (text, time) => (
  <Container>
    <Text>{text}</Text>
    <Time>{time}</Time>
  </Container>
)

export default CountDownItem
